body {
  border: 1px solid red;
  width: 100%;
  font-size: 62.5%;
  font-family: "Montserrat", sans-serif;
}

.container {
  border: 1px solid red;
  width: 80vw;
  margin: 0 auto;
}

@media screen and (max-width: 840px) {
  .big-header-links {
    display: none;
  }
}

@media screen and (min-width: 840px) {
  .big-header-links {
    display: flex;
  }
}

.small-header-links {
  display: none;
}

header {
  width: 100%;
  height: 7vh;
  margin-top: 3vh;
  margin-bottom: 3vh;
  font-size: 1.6em;
}

.header-buttons {
  display: flex;
  vertical-align: middle;
  font-weight: 700;
  height: 100%;
  float: left;
}

.header-buttons button {
  outline: none;
  border: none;
}

.theme-button {
  height: 100%;
  float: left;
}

.theme-button :first-child {
  height: 2em;
  width: 2em;
  margin-left: 25px;
  margin-right: 25px;
  vertical-align: middle;
}

.language-button {
  height: 100%;
  float: right;
}

.language-button :first-child {
  display: inline-block;
  height: 2em;
  width: 3em;
  margin-left: 25px;
  margin-right: 25px;
  vertical-align: middle;
}

.big-header-links {
  float: right;
  height: 100%;
}

.big-header-links ul {
  height: auto;
  float: right !important;
  align-self: center;
  list-style: none;
}

.big-header-links ul li {
  display: inline;
}

.big-header-links ul li a {
  padding: 0 25px;
  vertical-align: middle;
  font-weight: 700;
  display: inline-block;
}

.first-section {
  margin-top: 0px;
  height: 87vh !important;
}

.first-section-title {
  display: table;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 20px;
  font-size: 6vw;
}

.first-section-description {
  display: table;
  margin: 0 auto;
  text-align: center;
  font-size: 1.5vw;
  margin-bottom: 40px;
}

.first-section-label {
  position: relative;
  top: 25%;
  margin-left: 2em;
  margin-right: 2em;
}

.first-section-icons {
  display: table;
  margin: 0 auto;
}

.first-section-icons svg {
  vertical-align: middle;
  height: 40px;
  width: 40px;
  margin-left: 15px;
  margin-right: 15px;
}

.second-section {
  height: 500px;
  background-color: #2c2f33;
}

.second-section-margin {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: inherit;
  margin-left: 2em;
  margin-right: 2em;
}

.second-section-label {
  height: 300px;
}

.second-section-label-image {
  display: block-inline;
  float: left;
  height: inherit;
  margin-right: 2em;
}

.second-section-label img {
  height: inherit;
}

.second-section-label-text {
  float: right;
}

.second-section-label-text :first-child {
  margin-bottom: 1em;
  font-size: 3.5em;
}

.second-section-label-text :last-child {
  line-height: 1.6em;
  font-size: 1.7em;
}
